// app/careers/page.tsx
import type { Metadata } from "next";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { 
  Search,
  Calendar,
  MapPin,
  Users,
  Briefcase,
  Download,
  Clock,
  DollarSign,
  GraduationCap,
  FileText,
  Filter,
  SortAsc
} from "lucide-react";

export const metadata: Metadata = {
  title: "Careers | Kongoni Technical Vocational Training College",
  description: "Join our team at KTVC. Explore current job openings, career opportunities, and build your future in technical education.",
  keywords: "careers, jobs, employment, teaching positions, KTVC jobs, technical education careers",
};

interface JobOpening {
  title: string;
  department: string;
  location: string;
  type: string;
  deadline: string;
  postedDate: string;
  requirements: string[];
  description: string;
  salary?: string;
  experience: string;
  status: "open" | "closing-soon" | "closed";
  documentUrl: string;
  fileSize: string;
}

const jobOpenings: JobOpening[] = [
 
  {
    title: "Automotive Technology Instructor",
    department: "Automotive Department",
    location: "Kongoni Technical", 
    type: "Full-time",
    deadline: "25 Jun 2025",
    postedDate: "22 May 2025",
    requirements: ["Diploma/Degree in Automotive Technology", "Industry experience", "Teaching qualification"],
    description: "Deliver automotive technology courses and supervise practical sessions.",
    salary: "Kshs ",
    experience: "3+ years",
    status: "open",
    documentUrl: "/documents/automotive-instructor-2025.pdf",
    fileSize: "2.3 MB"
  },
  {
    title: "Library Assistant",
    department: "Library Services",
    location: "Kongoni Technical",
    type: "Full-time",
    deadline: "08 Jun 2025",
    postedDate: "15 May 2025",
    requirements: ["Diploma in Library Science", "Computer literacy", "Customer service skills"],
    description: "Assist students and staff with library services, catalog management, and research support.",
    salary: "Kshs ",
    experience: "1+ years",
    status: "closing-soon",
    documentUrl: "/documents/library-assistant-2025.pdf",
    fileSize: "1.5 MB"
  },
  {
    title: "Electrical Engineering Lecturer",
    department: "Engineering Department",
    location: "Kongoni Technical",
    type: "Full-time",
    deadline: "30 Jun 2025",
    postedDate: "25 May 2025",
    requirements: ["Masters in Electrical Engineering", "Teaching experience", "Research publications preferred"],
    description: "Teach electrical engineering courses and conduct research activities.",
    salary: "Kshs ",
    experience: "4+ years",
    status: "open",
    documentUrl: "/documents/electrical-lecturer-2025.pdf",
    fileSize: "2.0 MB"
  },
  {
    title: "Human Resource Assistant",
    department: "Human Resources",
    location: "Kongoni Technical",
    type: "Full-time",
    deadline: "20 Jun 2025",
    postedDate: "28 May 2025",
    requirements: ["Bachelor's in HR/Business", "HR certification", "2+ years experience"],
    description: "Support HR operations, recruitment processes, and employee relations.",
    salary: "Kshs ",
    experience: "2+ years",
    status: "open",
    documentUrl: "/documents/hr-assistant-2025.pdf",
    fileSize: "1.7 MB"
  }
];

const jobStats = [
  { label: "Open Positions", value: "8", icon: Briefcase },
  { label: "Departments Hiring", value: "6", icon: Users },
  { label: "Application Deadline", value: "Jun 2025", icon: Calendar },
  { label: "Average Response Time", value: "2 weeks", icon: Clock }
];

export default function CareersPage() {
  const getStatusBadge = (status: string) => {
    switch (status) {
      case "open":
        return <Badge variant="default" className="bg-green-500">Open</Badge>;
      case "closing-soon":
        return <Badge variant="destructive">Closing Soon</Badge>;
      case "closed":
        return <Badge variant="secondary">Closed</Badge>;
      default:
        return <Badge variant="outline">Unknown</Badge>;
    }
  };

  const formatDeadline = (deadline: string) => {
    const date = new Date(deadline);
    const now = new Date();
    const diffTime = date.getTime() - now.getTime();
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    
    if (diffDays < 0) return "Expired";
    if (diffDays === 0) return "Today";
    if (diffDays === 1) return "Tomorrow";
    return `${diffDays} days left`;
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Hero Section */}
      <section className="bg-gradient-to-r from-[#099cca] to-[#277DF5] text-white py-16">
        <div className="container mx-auto px-4">
          <div className="max-w-4xl mx-auto text-center">
            <h1 className="text-4xl md:text-5xl font-bold mb-6">
              Career Opportunities
            </h1>
            <p className="text-xl opacity-80 leading-relaxed mb-8">
              Join our dedicated team of educators and professionals. Build your career in technical and vocational education at KTVC.
            </p>
            <div className="flex flex-wrap justify-center gap-4">
              <Badge variant="secondary" className="text-lg px-4 py-2">
                8 Open Positions
              </Badge>
              <Badge variant="secondary" className="text-lg px-4 py-2">
                Competitive Benefits
              </Badge>
              <Badge variant="secondary" className="text-lg px-4 py-2">
                Professional Development
              </Badge>
            </div>
          </div>
        </div>
      </section>

      <div className="container mx-auto px-4 py-12">
        {/* Statistics */}
        <section className="mb-12">
          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
            {jobStats.map((stat, index) => (
              <Card key={index} className="text-center hover:shadow-lg transition-shadow">
                <CardContent className="p-6">
                  <stat.icon className="text-[#099cca] w-12 h-12 mx-auto mb-4" />
                  <h3 className="text-2xl font-bold text-gray-900 mb-2">
                    {stat.value}
                  </h3>
                  <p className="text-gray-600">{stat.label}</p>
                </CardContent>
              </Card>
            ))}
          </div>
        </section>

        {/* Search and Filter Section */}
        <section className="mb-8">
          <Card className="shadow-lg">
            <CardHeader>
              <CardTitle className="text-2xl text-gray-900">Search Career Opportunities</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid md:grid-cols-3 gap-4">
                <div className="relative">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
                  <input
                    type="text"
                    placeholder="Search job titles, departments..."
                    className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#099cca] focus:border-transparent"
                  />
                </div>
                <div className="relative">
                  <Filter className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
                  <select className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#099cca] focus:border-transparent appearance-none bg-white">
                    <option value="">All Departments</option>
                    <option value="engineering">Engineering</option>
                    <option value="automotive">Automotive</option>
                    <option value="it">Information Technology</option>
                    <option value="science">Science</option>
                    <option value="admin">Administration</option>
                  </select>
                </div>
                <div className="relative">
                  <SortAsc className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
                  <select className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#099cca] focus:border-transparent appearance-none bg-white">
                    <option value="">Sort by</option>
                    <option value="deadline">Deadline</option>
                    <option value="posted">Date Posted</option>
                    <option value="title">Job Title</option>
                    <option value="department">Department</option>
                  </select>
                </div>
              </div>
            </CardContent>
          </Card>
        </section>

        {/* Jobs Table */}
        <section className="mb-12">
          <Card className="shadow-lg">
            <CardHeader>
              <CardTitle className="text-2xl text-gray-900">Current Openings</CardTitle>
            </CardHeader>
            <CardContent className="p-0">
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead className="bg-[#B85450] text-white">
                    <tr>
                      <th className="px-6 py-4 text-left font-semibold">JOB TITLE</th>
                      <th className="px-6 py-4 text-left font-semibold">DEPARTMENT</th>
                      <th className="px-6 py-4 text-left font-semibold">DEADLINE</th>
                      <th className="px-6 py-4 text-left font-semibold">STATUS</th>
                      <th className="px-6 py-4 text-left font-semibold">DOCUMENT</th>
                    </tr>
                  </thead>
                  <tbody>
                    {jobOpenings.map((job, index) => (
                      <tr key={index} className={index % 2 === 0 ? "bg-gray-50" : "bg-white"}>
                        <td className="px-6 py-4">
                          <div>
                            <h3 className="font-semibold text-gray-900">{job.title}</h3>
                            <div className="flex items-center gap-4 text-sm text-gray-600 mt-1">
                              <div className="flex items-center gap-1">
                                <MapPin className="w-4 h-4" />
                                {job.location}
                              </div>
                              <div className="flex items-center gap-1">
                                <Briefcase className="w-4 h-4" />
                                {job.type}
                              </div>
                              {job.salary && (
                                <div className="flex items-center gap-1">
                                  <DollarSign className="w-4 h-4" />
                                  {job.salary}
                                </div>
                              )}
                            </div>
                          </div>
                        </td>
                        <td className="px-6 py-4">
                          <span className="text-gray-900">{job.department}</span>
                          <div className="text-sm text-gray-600 mt-1">
                            Experience: {job.experience}
                          </div>
                        </td>
                        <td className="px-6 py-4">
                          <div className="text-gray-900">{job.deadline}</div>
                          <div className="text-sm text-gray-600 mt-1">
                            {formatDeadline(job.deadline)}
                          </div>
                        </td>
                        <td className="px-6 py-4">
                          {getStatusBadge(job.status)}
                        </td>
                        <td className="px-6 py-4">
                          <button className="flex items-center gap-2 text-[#099cca] hover:text-[#277DF5] transition-colors">
                            <Download className="w-4 h-4" />
                            <div className="text-left">
                              <div className="text-sm font-medium">Download</div>
                              <div className="text-xs text-gray-500">{job.fileSize}</div>
                            </div>
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </CardContent>
          </Card>
        </section>

        {/* Job Details Modal/Expanded View */}
        <section className="mb-12">
          <div className="grid lg:grid-cols-2 gap-8">
            {/* Featured Job */}
            <Card className="shadow-lg">
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Briefcase className="text-[#099cca]" />
                  Featured Position
                </CardTitle>
              </CardHeader>
              <CardContent>
                <h3 className="text-xl font-bold text-gray-900 mb-2">
                  Senior Lecturer - Mechanical Engineering
                </h3>
                <div className="space-y-2 text-sm text-gray-600 mb-4">
                  <div className="flex items-center gap-2">
                    <Users className="w-4 h-4 text-[#099cca]" />
                    <span>Engineering Department</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <MapPin className="w-4 h-4 text-[#099cca]" />
                    <span>Kongoni Technical</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <DollarSign className="w-4 h-4 text-[#099cca]" />
                    <span>Kshs 80,000 - 120,000</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <Calendar className="w-4 h-4 text-[#099cca]" />
                    <span>Deadline: June 15, 2025</span>
                  </div>
                </div>
                <p className="text-gray-700 mb-4">
                  We are seeking an experienced mechanical engineering lecturer to join our dynamic team. The role involves teaching undergraduate courses, curriculum development, and student supervision.
                </p>
                <h4 className="font-semibold text-gray-900 mb-2">Key Requirements:</h4>
                <ul className="text-sm text-gray-700 space-y-1">
                  <li>• Masters degree in Mechanical Engineering</li>
                  <li>• Minimum 5 years teaching experience</li>
                  <li>• Professional registration preferred</li>
                  <li>• Research experience an advantage</li>
                </ul>
              </CardContent>
            </Card>

            {/* Application Process */}
            <Card className="shadow-lg">
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <FileText className="text-[#099cca]" />
                  Application Process
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <div className="flex items-start gap-3">
                    <div className="bg-[#099cca] text-white rounded-full w-8 h-8 flex items-center justify-center text-sm font-bold">
                      1
                    </div>
                    <div>
                      <h4 className="font-semibold text-gray-900">Review Requirements</h4>
                      <p className="text-sm text-gray-600">Download and carefully read the job description and requirements.</p>
                    </div>
                  </div>
                  <div className="flex items-start gap-3">
                    <div className="bg-[#099cca] text-white rounded-full w-8 h-8 flex items-center justify-center text-sm font-bold">
                      2
                    </div>
                    <div>
                      <h4 className="font-semibold text-gray-900">Prepare Documents</h4>
                      <p className="text-sm text-gray-600">Gather CV, cover letter, certificates, and references.</p>
                    </div>
                  </div>
                  <div className="flex items-start gap-3">
                    <div className="bg-[#099cca] text-white rounded-full w-8 h-8 flex items-center justify-center text-sm font-bold">
                      3
                    </div>
                    <div>
                      <h4 className="font-semibold text-gray-900">Submit Application</h4>
                      <p className="text-sm text-gray-600">Email your application to hr@kongonicollege.ac.ke before the deadline.</p>
                    </div>
                  </div>
                  <div className="flex items-start gap-3">
                    <div className="bg-[#099cca] text-white rounded-full w-8 h-8 flex items-center justify-center text-sm font-bold">
                      4
                    </div>
                    <div>
                      <h4 className="font-semibold text-gray-900">Interview Process</h4>
                      <p className="text-sm text-gray-600">Shortlisted candidates will be contacted for interviews within 2 weeks.</p>
                    </div>
                  </div>
                </div>

                <div className="mt-6 p-4 bg-blue-50 rounded-lg">
                  <h4 className="font-semibold text-gray-900 mb-2">Application Guidelines:</h4>
                  <ul className="text-sm text-gray-700 space-y-1">
                    <li>• Submit applications before 5:00 PM on the deadline</li>
                    <li>• Include "Position Title" in email subject</li>
                    <li>• Attach documents in PDF format only</li>
                    <li>• Maximum attachment size: 5MB</li>
                  </ul>
                </div>
              </CardContent>
            </Card>
          </div>
        </section>

        {/* Benefits Section */}
        <section className="mb-12">
          <h2 className="text-3xl font-bold text-gray-900 text-center mb-8">
            Why Work With Us?
          </h2>
          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
            <Card className="text-center hover:shadow-lg transition-shadow">
              <CardContent className="p-6">
                <GraduationCap className="text-[#099cca] w-12 h-12 mx-auto mb-4" />
                <h3 className="font-semibold text-gray-900 mb-2">Professional Growth</h3>
                <p className="text-gray-700 text-sm">Continuous learning and development opportunities</p>
              </CardContent>
            </Card>
            <Card className="text-center hover:shadow-lg transition-shadow">
              <CardContent className="p-6">
                <Users className="text-[#099cca] w-12 h-12 mx-auto mb-4" />
                <h3 className="font-semibold text-gray-900 mb-2">Great Team</h3>
                <p className="text-gray-700 text-sm">Collaborative and supportive work environment</p>
              </CardContent>
            </Card>
            <Card className="text-center hover:shadow-lg transition-shadow">
              <CardContent className="p-6">
                <DollarSign className="text-[#099cca] w-12 h-12 mx-auto mb-4" />
                <h3 className="font-semibold text-gray-900 mb-2">Competitive Benefits</h3>
                <p className="text-gray-700 text-sm">Attractive salary and comprehensive benefits package</p>
              </CardContent>
            </Card>
            <Card className="text-center hover:shadow-lg transition-shadow">
              <CardContent className="p-6">
                <MapPin className="text-[#099cca] w-12 h-12 mx-auto mb-4" />
                <h3 className="font-semibold text-gray-900 mb-2">Great Location</h3>
                <p className="text-gray-700 text-sm">Modern campus with excellent facilities</p>
              </CardContent>
            </Card>
          </div>
        </section>

        {/* Contact Section */}
        <section>
          <Card className="bg-gradient-to-r from-[#099cca]/10 to-[#277DF5]/10">
            <CardContent className="p-8 text-center">
              <h2 className="text-2xl font-bold text-gray-900 mb-4">
                Have Questions About Our Opportunities?
              </h2>
              <p className="text-gray-700 mb-6 max-w-2xl mx-auto">
                Contact our Human Resources team for more information about career opportunities, application process, or general inquiries.
              </p>
              <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4 text-sm">
                <div className="flex items-center justify-center gap-2">
                  <Users className="text-[#099cca]" size={16} />
                  <span>hr@kongonicollege.ac.ke</span>
                </div>
                <div className="flex items-center justify-center gap-2">
                  <Clock className="text-[#099cca]" size={16} />
                  <span>+254 700 123 789</span>
                </div>
                <div className="flex items-center justify-center gap-2">
                  <MapPin className="text-[#099cca]" size={16} />
                  <span>HR Office, Administration Block</span>
                </div>
              </div>
            </CardContent>
          </Card>
        </section>
      </div>
    </div>
  );
}